.loading {
    margin: auto;
    width: 60px;
    height: 20px;
}

.dot1, .dot2, .dot3 {
    display: inline-block;
    width: 10px;
    height: 10px;
    background-color: lightslategrey;
    border-radius: 50%;
    margin-left: 10px;
}

.dot1 {
    margin: 0;
    animation: dot1 1s linear infinite;
}

.dot2 {
    animation: dot2 1s linear infinite;
}

.dot3 {
    animation: dot3 1s linear infinite;
}

@keyframes dot1 {
    0% {
        transform: none;
    }
    16.66% {
        transform: scale(1.4);
    }
    33.33% {
        transform: none;
    }
}

@keyframes dot2 {
    0% {
        transform: none;
    }
    33.33% {
        transform: none;
    }
    49.99% {
        transform: scale(1.4);
    }
    66.65% {
        transform: none;
    }
}

@keyframes dot3 {
    0% {
        transform: none;
    }
    66.65% {
        transform: none;
    }
    83.31% {
        transform: scale(1.4);
    }
    100% {
        transform: none;
    }
}